.image {
    background-size: contain;
    width: 50px;
    height: 50px;
    border-radius: 50px;
    margin-right: 12px;
    background-repeat: no-repeat;
    background-position: center center;
}

.churche-of-the-chain {
    background-image: url('./assets/images/drawer/churche-of-the-chain.jpg');
}

.nfy {
